<template>
  <StompBoss
    label="Tremolo"
    model="TR-2"
    class="tremolo"
    :input-outer="input"
    :output-outer="output"
    :input-inner="inNode"
    :output-inner="outNode"
  >
    <div class="pedal-controls">
      <div class="pedal-controls-row">
        <PolyKnob
          :min="0"
          :max="10"
          @change="setSpeed"
          step="0.1"
          :default-value="3.5"
          type="good_boss"
          label="Rate"
        />
        <PolyKnob
          :min="0"
          :max="1"
          label="Depth"
          type="good_boss"
          @change="setMix"
          :default-value="0.75"
        />
      </div>
      <PolyKnob
        :min="0"
        :max="1"
        reversed
        width="48"
        height="48"
        label="Wave"
        type="boss_small1"
        @change="setWave"
        class="m-align"
        :default-value="0.8"
      />
    </div>
  </StompBoss>
</template>

<script>
import ctx from "../../web-audio/audio-context.js";
import io from "../../utils/io";
import StompBoss from "./StompBoss";
import PolyKnob from "../elements/PolyKnob";

export default {
  components: {
    StompBoss,
    PolyKnob,
  },
  props: { ...io },
  data() {
    return {
      inNode: ctx.createGain(),
      outNode: ctx.createGain(),
      dryNode: ctx.createGain(),
      tremoloGain: ctx.createGain(),
      mix: 0.5,
      speed: 3,
      wetNode: ctx.createGain(),
      oscillatorSin: ctx.createOscillator(),
      oscillatorSinGain: ctx.createGain(),
      oscillatorTriangle: ctx.createOscillator(),
      oscillatorTriangleGain: ctx.createGain(),
      mergeGain: ctx.createGain()
    };
  },
  created() {
    this.build();
  },
  methods: {
    setMix(value) {
      this.mix = value;
      this.wetNode.gain.setValueAtTime(value, ctx.currentTime);
      this.dryNode.gain.setValueAtTime(1 - value, ctx.currentTime);
    },
    setSpeed(value) {
      const syncTime = ctx.currentTime+0.01;
      this.speed = value;
      this.oscillatorSin.frequency.setValueAtTime(value, syncTime);
      this.oscillatorTriangle.frequency.setValueAtTime(value, syncTime);
    },
    setWave(value) {
      this.oscillatorSinGain.gain.value = value;
      this.oscillatorTriangleGain.gain.value = 1 - value;
    },
    build() {
      this.oscillatorSinGain.gain.value = 0.5
      this.oscillatorSin.frequency.setValueAtTime(this.speed, ctx.currentTime);
      this.oscillatorSin.connect(this.oscillatorSinGain);
      this.oscillatorSinGain.connect(this.mergeGain);

      this.oscillatorTriangleGain.gain.value = 0.5
      this.oscillatorTriangle.type = "triangle"
      this.oscillatorTriangle.frequency.setValueAtTime(this.speed, ctx.currentTime);
      this.oscillatorTriangle.connect(this.oscillatorTriangleGain);
      this.oscillatorTriangleGain.connect(this.mergeGain);

      this.mergeGain.connect(this.tremoloGain.gain);
      
      const syncTime = ctx.currentTime+0.01;
      this.oscillatorSin.start(syncTime);
      this.oscillatorTriangle.start(syncTime);

      this.wetNode.gain.setValueAtTime(this.mix, ctx.currentTime);
      this.dryNode.gain.setValueAtTime(1 - this.mix, ctx.currentTime);
      this.inNode.connect(this.dryNode).connect(this.outNode);
      this.inNode
        .connect(this.tremoloGain)
        .connect(this.wetNode)
        .connect(this.outNode);
    },
  },
};
</script>

