<template>
  <div>
    <Amp :input="pedalboardOut" :output="ampOut" :glowSwitch="glowSwitch">
      <slot></slot>
    </Amp>
    <Pedalboard :input="input" :output="pedalboardOut" />
  </div>
</template>

<script>
import Amp from "./Amp";
import Pedalboard from "./Pedalboard";
import ctx from "../web-audio/audio-context.js";

export default {
  name: "Stack",
  components: {
    Amp,
    Pedalboard,
  },
  props: {
    input: {
      type: AudioNode,
    },
    glowSwitch: {
      type: Boolean
    }
  },
  data() {
    const ampOut = ctx.createGain();
    const pedalboardOut = ctx.createGain();
    ampOut.connect(ctx.destination);

    return {
      ampOut,
      pedalboardOut,
    };
  },
};
</script>

