<template>
  <div>
    <Stack :input="input" :glowSwitch="glowSwitch">
      <div class="left-block">
        <img
          class="logo"
          :class="{ attract: glowInput }"
          src="~@/assets/img/input.png"
          @click="
            showModal = true;
            glowInput = false;
          "
        />
        <label class="input-label" for="">input</label>
        <Modal v-if="showModal" :value="source" @select="selectSource" />
      </div>
    </Stack>
  </div>
</template>

<script>
import Stack from "./components/Stack.vue";
import ctx from "./web-audio/audio-context";
import Modal from "@/components/Modal";

export default {
  name: "App",
  components: {
    Stack,
    Modal,
  },
  data() {
    const input = ctx.createGain();
    return {
      input,
      source: {
        type: "demo",
      },
      showModal: false,
      glowInput: true,
      glowSwitch: false,
    };
  },
  created() {
    fetch("audio/demo.mp3")
      .then((response) => response.arrayBuffer())
      .then((arrayBuffer) => ctx.decodeAudioData(arrayBuffer))
      .then((audioBuffer) => {
        this.sampleBuffer = audioBuffer;
      })
      .then(this.selectSource);
  },
  methods: {
    async selectSource(event) {
      this.showModal = false;
      if (event) {
        this.glowSwitch = true;
        if (event.type === this.source.type) {
          return;
        }
        this.source = event;
      }
      if (event && event.type === "mic") {
        if (!this.inputStream) {
          this.inputStream = await navigator.mediaDevices.getUserMedia({
            audio: {
              echoCancellation: false,
            },
            video: false,
          });

          this.inputSource = ctx.createMediaStreamSource(this.inputStream);
        }
        this.decreasedGain.disconnect(this.input);
        const splitter = ctx.createChannelSplitter(2);
        this.merger = ctx.createChannelMerger(2);

        this.inputSource.connect(splitter);
        splitter.connect(this.merger, 0, 0);
        splitter.connect(this.merger, 0, 1);
        this.merger.connect(this.input);
      } else {
        this.decreasedGain = ctx.createGain();
        this.decreasedGain.gain.value = 0.1;
        if (!this.sampleSource) {
          this.sampleSource = ctx.createBufferSource();
          this.sampleSource.buffer = this.sampleBuffer;
          this.sampleSource.loop = true;
          this.sampleSource.start();
        }
        if (this.inputStream) {
          this.merger.disconnect(this.input);
        }

        this.sampleSource.connect(this.decreasedGain);
        this.decreasedGain.connect(this.input);
      }
    },
  },
};
</script>

