<template>
  <header>
    <slot></slot>
    <!-- <div class="dd-column">
      <label>Source</label>
      <select v-on:change="selectSource($event)">
        <option value="samples">samples</option>
        <option value="mic">audio input</option>
      </select>
    </div> -->
    <div class="controls">
      <div class="control">
        <PolyKnob
          type="marshall"
          :defaultValue="initialVolume"
          @change="updateVolume"
          min="0"
          max="5"
          step="0.1"
          label="volume"
          width="100"
          height="100"
        />
      </div>
      <div class="control">
        <PolyKnob
          type="marshall"
          :defaultValue="initialGain"
          @change="updateGain"
          min="0"
          max="500"
          label="gain"
          width="100"
          height="100"
        />
      </div>
      <div class="control">
        <PolyKnob
          type="marshall"
          :defaultValue="initialBass"
          min="-20"
          max="20"
          @change="updateBass"
          label="bass"
          width="100"
          height="100"
        />
      </div>
      <div class="control">
        <PolyKnob
          type="marshall"
          :defaultValue="initialMid"
          min="-20"
          max="20"
          @change="updateMid"
          label="mid"
          width="100"
          height="100"
        />
      </div>
      <div class="control">
        <PolyKnob
          type="marshall"
          :defaultValue="initialTreble"
          min="-20"
          max="20"
          @change="updateTreble"
          label="high"
          width="100"
          height="100"
        />
      </div>
      <div class="control">
        <PolyKnob
          type="marshall"
          label="reverb"
          min="0"
          max="1"
          step="0.01"
          @change="updateReverb"
          :defaultValue="initialReverb"
          width="100"
          height="100"
        />
      </div>
      <div class="control">
        <PolyKnob
          type="marshall"
          :defaultValue="initialPresence"
          @change="updatePresence"
          min="-10"
          max="10"
          label="presence"
          width="100"
          height="100"
        />
      </div>
      <div class="control">
        <PolyKnob
          type="marshall"
          :defaultValue="1"
          @change="updateWet"
          min="0"
          max="1"
          step="0.01"
          label="dry/wet"
          width="100"
          height="100"
        />
      </div>
    </div>
    <div class="toggle" :class="{ on: turnedOn, attract: glowSwitch && neverTunrnedOn }" @click="toggle"></div>
  </header>
</template>

<script>
import ctx from "../web-audio/audio-context.js";
import wsFactory from "../web-audio/ws-factory.js";
import PolyKnob from "@/components/elements/PolyKnob";
const initialVolume = 2;
const initialBass = 0;
const initialMid = 0;
const initialTreble = 0;
const initialPresence = 5;
const initialGain = 50;
const initialReverb = 0.4;
export default {
  components: { PolyKnob },
  computed: {
    initialGain() {
      return initialGain;
    },
    initialVolume() {
      return initialVolume;
    },
    initialBass() {
      return initialBass;
    },
    initialTreble() {
      return initialTreble;
    },
    initialMid() {
      return initialMid;
    },
    initialPresence() {
      return initialPresence;
    },
    initialReverb() {
      return initialReverb;
    },
  },
  props: {
    input: {
      type: AudioNode,
      requied: true,
    },
    output: {
      type: AudioNode,
      requied: true,
    },
    glowSwitch: {
      type: Boolean
    }
  },
  data() {
    let showModal = false;

    const cabinetConvolner = ctx.createConvolver();
    const od = [];
    const volume = ctx.createGain();
    const reverb = {
      dry: ctx.createGain(),
      wet: ctx.createGain(),
      mix: ctx.createGain(),
      convolver: ctx.createConvolver(),
    };
    reverb.wet.gain.value = initialReverb;
    reverb.dry.gain.value = 1 - initialReverb;
    volume.gain.value = initialVolume;
    const dry = ctx.createGain();
    const wet = ctx.createGain();
    dry.gain.value = 0;
    // JCM 800 preamp schematic...
    //
    // Low shelf cut -6db à 720Hz
    const lowShelf1 = ctx.createBiquadFilter();
    lowShelf1.type = "lowshelf";
    lowShelf1.frequency.value = 720;
    lowShelf1.gain.value = -6;

    // Low shelf cut variable wired to volume knob
    // if vol = 50%, then filter at -6db, 320Hz
    // shoud go from -4db to -6db for +/- fatness
    const lowShelf2 = ctx.createBiquadFilter();
    lowShelf2.type = "lowshelf";
    lowShelf2.frequency.value = 320;
    lowShelf2.gain.value = -5;

    od[0] = ctx.createWaveShaper();
    od[0].curve = wsFactory.distorsionCurves["asymetric"](initialGain); // 'asymetric'

    const highPass1 = ctx.createBiquadFilter();
    highPass1.type = "highpass";
    highPass1.frequency.value = 6;
    highPass1.Q.value = 0.7071;

    // lowshelf cut -6db 720Hz
    const lowShelf3 = ctx.createBiquadFilter();
    lowShelf3.type = "lowshelf";
    lowShelf3.frequency.value = 720;
    lowShelf3.gain.value = -6;

    od[1] = ctx.createWaveShaper();
    od[1].curve = wsFactory.distorsionCurves["standard"](initialGain); //'standard'

    const bassFilter = ctx.createBiquadFilter();
    bassFilter.frequency.value = 100;
    bassFilter.type = "lowshelf";
    bassFilter.Q.value = 0.7071; // To check with Lepou
    bassFilter.gain.value = initialBass;

    const midFilter = ctx.createBiquadFilter();
    midFilter.frequency.value = 1700;
    midFilter.type = "peaking";
    midFilter.Q.value = 0.7071; // To check with Lepou
    midFilter.gain.value = initialMid;

    const trebleFilter = ctx.createBiquadFilter();
    trebleFilter.frequency.value = 6500;
    trebleFilter.type = "highshelf";
    trebleFilter.Q.value = 0.7071; // To check with Lepou
    trebleFilter.gain.value = initialTreble;

    const presenceFilter = ctx.createBiquadFilter();
    presenceFilter.frequency.value = 3900;
    presenceFilter.type = "peaking";
    presenceFilter.Q.value = 0.7071; // To check with Lepou
    presenceFilter.gain.value = initialPresence;

    // -----------------------------------
    // POST PROCESSING STAGE (EQ, reverb)
    // -----------------------------------
    //  before EQ, filter highs and lows (Fred Miniere advise)
    const eqhicut = ctx.createBiquadFilter();
    eqhicut.frequency.value = 10000;
    eqhicut.type = "peaking";
    eqhicut.gain.value = -25;

    const eqlocut = ctx.createBiquadFilter();
    eqlocut.frequency.value = 60;
    eqlocut.type = "peaking";
    eqlocut.gain.value = -19;

    this.input.connect(dry);
    this.input.connect(volume);
    volume.connect(lowShelf1);
    lowShelf1.connect(lowShelf2);
    lowShelf2.connect(od[0]);
    od[0].connect(highPass1);
    highPass1.connect(lowShelf3);
    lowShelf3.connect(od[1]);
    od[1].connect(trebleFilter);
    trebleFilter.connect(bassFilter);
    bassFilter.connect(midFilter);
    midFilter.connect(presenceFilter);

    // lo and hicuts
    presenceFilter.connect(eqlocut);
    eqlocut.connect(eqhicut);

    // bypass eq route
    eqhicut.connect(cabinetConvolner);

    cabinetConvolner.connect(reverb.wet);
    cabinetConvolner.connect(reverb.dry);
    reverb.wet.connect(reverb.convolver);
    reverb.convolver.connect(reverb.mix);
    reverb.dry.connect(reverb.mix);
    reverb.mix.connect(wet);
    dry.connect(this.output);
    wet.connect(this.output);

    return {
      od,
      turnedOn: false,
      cabinetConvolner,
      bassFilter,
      midFilter,
      trebleFilter,
      presenceFilter,
      volume,
      reverb,
      showModal,
      dry,
      wet,
      neverTunrnedOn: true,
    };
  },
  created() {
    fetch("audio/impulse.wav") // "impulse.wav"
      .then((response) => response.arrayBuffer())
      .then((buffer) => ctx.decodeAudioData(buffer))
      .then((impulse) => (this.cabinetConvolner.buffer = impulse));

    fetch("audio/reverb.wav")
      .then((response) => response.arrayBuffer())
      .then((buffer) => ctx.decodeAudioData(buffer))
      .then((impulse) => (this.reverb.convolver.buffer = impulse));
  },
  methods: {
    toggle() {
      this.neverTunrnedOn = false;
      ctx.resume();
      this.turnedOn = !this.turnedOn;
      this.input.gain.setValueAtTime(Number(this.turnedOn), ctx.currentTime);
    },
    updateVolume(value) {
      this.volume.gain.value = value;
    },
    updateGain(k) {
      this.od[0].curve = wsFactory.distorsionCurves["asymetric"](k); // 'asymetric'
      this.od[1].curve = wsFactory.distorsionCurves["standard"](k); //'standard'
    },
    updatePresence(value) {
      this.presenceFilter.gain.value = value;
    },
    updateBass(value) {
      this.bassFilter.gain.value = value;
    },
    updateMid(value) {
      this.midFilter.gain.value = value;
    },
    updateTreble(value) {
      this.trebleFilter.gain.value = value;
    },
    updateReverb(value) {
      this.reverb.wet.gain.value = value;
      this.reverb.dry.gain.value = 1 - value;
    },
    updateWet(value) {
      this.wet.gain.value = value;
      this.dry.gain.value = 1 - value;
    },
  },
};
</script>

