<script>
import "../../lib/webaudio-controls";

export default {
  props: {
    min: {
      type: Number,
      default: 0,
    },
    max: {
      type: Number,
      default: 100,
    },
    label: {
      type: String,
      default: "label",
    },
    type: {
      type: String,
      default: "boss",
    },
    width: {
      type: Number,
      default: 75,
    },
    height: {
      type: Number,
      default: 75,
    },
    defaultValue: {
      type: Number,
    },
    reversed: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    sprite() {
      return require(`@/assets/img/${this.type}.png`);
    },
  },
  methods: {
    onInput(event) {
      this.$emit("change", event.target.value);
    },
  },
};
</script>

<template>
  <div class="knob">
    <label v-if="reversed">{{ label }}</label>
    <webaudio-knob
      class="knob-control"
      :min="min"
      :src="sprite"
      :max="max"
      :step="0.01"
      :width="width"
      :value="defaultValue"
      :height="height"
      @input="onInput"
    ></webaudio-knob>
    <label v-if="!reversed">{{ label }}</label>
  </div>
</template>

<style lang="scss" scoped>
.knob {
  background: none;
  text-align: center;
  label {
    display: inline-block;
    font-family: Aktiv Grotesk Ex;
    font-style: normal;
    font-weight: bold;
    font-size: 13px;
    line-height: 17px;
    /* identical to box height */

    text-align: center;
  }
  .knob-control {
    filter: drop-shadow(0px 4px 5px rgba(0, 0, 0, 0.25))  drop-shadow(10px 10px 25px rgba(0, 0, 0, 0.4));
    -webkit-filter: drop-shadow(0px 4px 5px rgba(0, 0, 0, 0.25)) drop-shadow(10px 10px 25px rgba(0, 0, 0, 0.4));
  }
}
</style>