<template>
  <StompBoss
    label="Distortion"
    model="DS-1"
    class="distortion"
    :input-outer="input"
    :output-outer="output"
    :input-inner="inNode"
    :output-inner="outNode"
  >
    <div class="pedal-controls">
      <div class="pedal-controls-row">
        <PolyKnob
          :min="-10"
          :max="10"
          @change="setTone"
          :default-value="2"
          type="good_boss"
          label="Tone"
        />
        <PolyKnob
          :min="0"
          :max="1500"
          label="Dist"
          type="good_boss"
          @change="setDist"
          :default-value="400"
        />
      </div>
      <PolyKnob
        :min="0"
        :max="1"
        reversed
        width="48"
        height="48"
        label="Level"
        type="boss_small1"
        @change="setLevel"
        class="m-align"
        :default-value="0.5"
      />
    </div>
  </StompBoss>
</template>

<script>
import ctx from "../../web-audio/audio-context";
import wsFactory from "../../web-audio/ws-factory";
import io from "../../utils/io";
import StompBoss from "./StompBoss";
import PolyKnob from "../elements/PolyKnob";

export default {
  props: { ...io },
  components: { StompBoss, PolyKnob },
  data() {
    return {
      inNode: ctx.createWaveShaper(),
      outNode: ctx.createGain(),
      tone: ctx.createBiquadFilter(),
      effect: 150,
      volume: 0.5,
    };
  },
  created() {
    this.build();
  },
  methods: {
    setDist(effect) {
      this.effect = effect;
      this.inNode.curve = wsFactory.distorsionCurves["standard"](effect);
    },
    setLevel(volume) {
      this.volume = volume;
      this.outNode.gain.setValueAtTime(volume, ctx.currentTime);
    },
    setTone(tone) {
      this.tone.gain.value = tone;
    },
    build() {
      this.tone.frequency.value = 3900;
      this.tone.type = "peaking";
      this.tone.Q.value = 0.7071;
      this.tone.gain.value = 2;

      this.inNode.curve = wsFactory.distorsionCurves["standard"](this.effect);
      this.outNode.gain.setValueAtTime(this.volume, ctx.currentTime);

      this.inNode.connect(this.tone);
      this.tone.connect(this.outNode);
    },
  },
};
</script>

