<template>
  <StompBoss
    label="OverDrive"
    model="OD-3"
    class="overdrive"
    :input-outer="input"
    :output-outer="output"
    :input-inner="inNode"
    :output-inner="outNode"
  >
    <div class="pedal-controls">
      <div class="pedal-controls-row">
        <PolyKnob
          :min="0"
          :max="2"
          @change="setVolume"
          :default-value="1"
          type="good_boss"
          label="Level"
        />
        <PolyKnob
          :min="0"
          :max="1500"
          label="Drive"
          type="good_boss"
          @change="setDist"
          :default-value="700"
        />
      </div>
      <PolyKnob
        reversed
        width="48"
        height="48"
        label="Tone"
        type="boss_small1"
        :min="-10"
        :max="10"
        @change="setTone"
        :default-value="2"
        class="m-align"
      />
    </div>
  </StompBoss>
</template>

<script>
import ctx from "../../web-audio/audio-context.js";
import io from "../../utils/io";
import StompBoss from "./StompBoss";
import PolyKnob from "../elements/PolyKnob";

export default {
  props: { ...io },
  components: { StompBoss, PolyKnob },
  data() {
    return {
      inNode: ctx.createWaveShaper(),
      outNode: ctx.createGain(),
      tone: ctx.createBiquadFilter(),
      effect: 150,
      volume: 0.5,
    };
  },
  created() {
    this.build();
  },
  methods: {
    setDist(effect) {
      this.effect = effect;
      this.inNode.curve = this.makeDistortionCurve(effect);
    },
    setVolume(volume) {
      this.volume = volume;
      if (this.outNode) {
        this.outNode.gain.setValueAtTime(volume, ctx.currentTime);
      }
    },
    setTone(tone) {
      this.tone.gain.value = tone;
    },
    build() {
      this.tone.frequency.value = 3900;
      this.tone.type = "peaking";
      this.tone.Q.value = 0.7071;
      this.tone.gain.value = 2;

      this.inNode.curve = this.makeDistortionCurve(this.effect);
      this.outNode.gain.setValueAtTime(this.volume, ctx.currentTime);
      this.inNode.connect(this.tone);
      this.tone.connect(this.outNode);
    },
    makeDistortionCurve(amount) {
      let k = typeof amount === "number" ? amount : 50,
        n_samples = 44100,
        curve = new Float32Array(n_samples),
        deg = Math.PI / 180,
        i = 0,
        x;
      for (; i < n_samples; ++i) {
        x = (i * 2) / n_samples - 1;
        curve[i] = ((3 + k) * x * 20 * deg) / (Math.PI + k * Math.abs(x));
      }
      return curve;
    },
  },
};
</script>

