<template>
  <div class="pedal">
    <div class="led">
      <div class="led-label">check</div>
      <div class="led-diode" :class="{ on: turnedOn }"></div>
    </div>
    <div class="controls">
      <slot></slot>
    </div>
    <div class="top">
      <div class="io">
        <div class="left"><i class="arrow"></i><span>Output</span></div>
        <div class="right"><span>Input</span><i class="arrow right"></i></div>
      </div>
      <div class="label">
        <div class="title">{{label}}</div>
        <div class="symbol">{{model}}</div>
      </div>
      <div class="rub" @click="toggle"></div>
    </div>
    <div class="battery-knob-row">
      <div class="battery-knob"></div>
    </div>
  </div>
</template>

<script>
import ctx from "../../web-audio/audio-context.js";

export default {
  components: {},
  props: {
    inputOuter: {
      type: AudioNode,
      required: true,
    },
    outputOuter: {
      type: AudioNode,
      required: true,
    },
    inputInner: {
      type: AudioNode,
      required: true,
    },
    outputInner: {
      type: AudioNode,
      required: true,
    },
    label: {
      type: String,
      required: true,
    },
    model: {
      type: String,
      required: true,
    },
  },
  data() {
    let bypass = ctx.createGain();
    bypass.gain.setValueAtTime(1, ctx.currentTime);
    let effect = ctx.createGain();
    effect.gain.setValueAtTime(0, ctx.currentTime);
    return {
      turnedOn: false,
      bypass,
      effect,
    };
  },
  created() {
    // create bypass route
    this.inputOuter.connect(this.bypass);
    this.bypass.connect(this.outputOuter);

    // create effect route
    this.inputOuter.connect(this.effect);
    this.effect.connect(this.inputInner);
    this.outputInner.connect(this.outputOuter);
  },
  methods: {
    toggle() {
      this.turnedOn = !this.turnedOn;
      this.effect.gain.setValueAtTime(Number(this.turnedOn), ctx.currentTime);
      this.bypass.gain.setValueAtTime(Number(!this.turnedOn), ctx.currentTime);
    },
  },
};
</script>

